import React from 'react'
import DataGrid, { TextEditor, Row } from 'react-data-grid'

interface Row {
  id: number;
  name: string;
  editor?: typeof TextEditor;
}

const columns = [
  { key: 'id', name: 'ID' },
  { key: 'title', name: 'Title', editor: TextEditor }
];

const rows = [
  { id: 0, title: 'Example' },
  { id: 1, title: 'Demo' }
];

const rowKeyGetter = (row: Row) => {
  return row.id
}

export const ReactGrid = () => {
  console.log(columns, rows)

  return (
    <DataGrid columns={columns} rows={rows} rowKeyGetter={rowKeyGetter} />
  )
}
